.score-board {
  position: absolute;
  top: 0;
  width: 125px;
  height: 125px;
  cursor: pointer;

  display: flex;
  flex-direction: row;
  align-items: center;
}

.score-board p {
  font-size: 96px;
  padding-bottom: 7.5px;

  margin: 0 auto;
}

.left-score {
  justify-content: flex-start;
  border-bottom-right-radius: 50%;
  left: 0;
}

.right-score {
  justify-content: flex-end;
  border-bottom-left-radius: 50%;
  right: 0;
}
.boxes {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3.5px;

  position: absolute;
  width: 501px;
  height: 501px;
}

.box {
  width: 160px;
  height: 160px;
  cursor: pointer;
}

svg {
  stroke: rgb(0, 0, 0);
  width: 501px;
  height: 501px;
  stroke-width: 10;
  stroke-linecap: round;
  stroke-linejoin: round;
  fill: none;
}

line {
  stroke: white;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash 1.25s cubic-bezier(0.65, 0, 0.45, 1) forwards;
          animation: dash 1.25s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

circle {
  stroke-width: 12.5;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash 1s cubic-bezier(0.65, 0, 0.45, 1) forwards;
          animation: dash 1s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

path {
  stroke-width: 12.5;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash 1s cubic-bezier(0.65, 0, 0.45, 1) forwards;
          animation: dash 1s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

@-webkit-keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

@media (max-width: 768px) {
  .boxes {
    grid-gap: 2px;
    width: 364px;
    height: 364px;
  }

  .box {
    width: 120px;
    height: 120px;
    cursor: pointer;
  }

  svg {
    width: 364px;
    height: 364px;
  }
}

@media (max-width: 481px) {
  .boxes {
    grid-gap: 1px;
    width: 242px;
    height: 242px;
  }

  .box {
    width: 80px;
    height: 80px;
    cursor: pointer;
  }

  svg {
    width: 242px;
    height: 242px;
  }

  circle {
    stroke-width: 7.5;
  }

  path {
    stroke-width: 7.5;
  }
}
.settings-button, .about-button {
  width: 125px;
  height: 125px;
  position: absolute;
  bottom: 0;
  cursor: pointer;
}

.settings-button {
  border-top-right-radius: 125px;
  left: 0;
}

.about-button {
  border-top-left-radius: 125px;
  right: 0;
}

.settings-button .button-icon {
  position: absolute;
  bottom: 25px;
  left: 25px;
  font-size: 48px;
}

.about-button .button-icon {
  position: absolute;
  bottom: 25px;
  right: 25px;
  font-size: 48px;
}

/* SWEET ALERT */

.see-github {
  text-decoration: none;
  font-weight: 600;
  display: flex;
}

/* OPTIONS */
.options-items {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 25px;
}

.options-items:first-child {
  margin-top: 25px;
}

.options-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  width: 100px;
  height: 100px;
  padding: 10px;
}

.options-item ion-icon {
  margin-bottom: 10px;
  font-size: 48px;
}

.active {
  border-radius: 15px;
  width: 100px;
  height: 100px;
  padding: 10px;
  color: white;
}
.game {
  height: 100vh;
  width: 100vw;
  color: white;
  text-align: center;
  position: relative;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
:root {
  --light-primary: #1F3A93;
  --light-secondary: #2196f3;
  --light-tertiary: #2B77C0;
  --light-first: #36DBD7;
  --light-second: #F62459;
  
  --dark-primary: #263238;
  --dark-secondary: #424242;
  --dark-tertiary: #263238;
  --dark-first: #FF6D00;
  --dark-second: #00C853;
}

.light {
  background-color: #1F3A93;
  background-color: var(--light-primary);
}

.dark {
  background-color: #263238;
  background-color: var(--dark-primary);
}

 /* SCORE */

 .light .left-score {
  background-color: #36DBD7;
  background-color: var(--light-first);
}

.dark .left-score {
  background-color: #FF6D00;
  background-color: var(--dark-first);
}

.light .right-score {
  background-color: #F62459;
  background-color: var(--light-second);
}

.dark .right-score {
  background-color: #00C853;
  background-color: var(--dark-second);
}

/* PAWN */

.light circle {
  stroke: #F62459;
  stroke: var(--light-second);
}

.dark circle {
  stroke: #00C853;
  stroke: var(--dark-second);
}

.light path {
  stroke: #36DBD7;
  stroke: var(--light-first);
}

.dark path {
  stroke: #FF6D00;
  stroke: var(--dark-first);
}

/* INTERACT */

.light .settings-button {
  background-color: #F62459;
  background-color: var(--light-second);
}

.dark .settings-button {
  background-color: #00C853;
  background-color: var(--dark-second);
}

.light .about-button {
  background-color: #36DBD7;
  background-color: var(--light-first);
}

.dark .about-button {
  background-color: #FF6D00;
  background-color: var(--dark-first);
}

/* SWEET ALERT */

.link-light {
  color: #2196f3;
  color: var(--light-secondary);
}

.light .active {
  background-color: #2B77C0;
  background-color: var(--light-tertiary);
}

.link-dark {
  color: #424242;
  color: var(--dark-secondary);
}

.dark .active {
  background-color: #263238;
  background-color: var(--dark-tertiary);
}

/* INTERACT */

.light .see-github {
  color: #2196f3;
  color: var(--light-secondary);
}

.item-light .active {
  background-color: #2B77C0;
  background-color: var(--light-tertiary);
}

.dark .see-github {
  color: #424242;
  color: var(--dark-secondary);
}

.item-dark .active {
  background-color: #263238;
  background-color: var(--dark-tertiary);
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
}

.home {
  height: 100vh;
  width: 100vw;
  background-color: #044D6E;
}
